@import '~antd/dist/antd.css';

$transitionDuration: 0.35s;

body {
  background: #f0f2f5;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

.layout .logo {
  font-size: 20px;
  margin: 16px 24px 16px 0;
  float: left;
  color: white;
  text-align: center;
  line-height: 30px;
}

.stock-item {
  border: 1px solid #EEE;
  border-radius: 3px;
  padding: 10px;
  background: #FFFFFF;
  transition: opacity $transitionDuration;
  position: relative;
  margin-bottom: 10px;
  overflow: hidden;

  opacity: 1;

  .item-description {
    overflow-y: scroll;
    opacity: 1;
    position: absolute;
    top: 94px;
    transition: opacity $transitionDuration*3;
    left: 0;
    width: 100%;
    background: #FCFCFC;
    height: calc(100% - 94px);
    padding: 10px;

    &.hidden {
      opacity: 0;
    }
  }

  &.hidden {
    opacity: 0;
    pointer-events: none;
  }

  &.enlarged {
  }

  .main {
    padding-right: 140px;

    & > * {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .name {
      font-weight: bold;
      font-size: 20px;
      margin-bottom: 4px;
      cursor: pointer;

      .return-icon {
        transition: all $transitionDuration;
        display: inline-block;
        margin-left: -2px;
        margin-right: -2px;
        width: 0;
        padding-right: 0;
        color: #CCC;
        white-space: nowrap;
        overflow: hidden;
        vertical-align: -8px;
      }
    }

    .price {
      font-weight: 500;
    }

    .purchased {
      opacity: .7;
      position: relative;

      .stock-count {
        font-size: 10px;
        opacity: 0.5;
        padding-left: 4px;
      }

      .ant-spin {
        position: absolute;
        margin-top: 2px;
      }
    }
  }

  &.enlarged {
    .main .name .return-icon {
      width: 24px;
    }
  }

  .side {
    position: absolute;
    right: 10px;
    top: 10px;

    .purchase-button.disabled {
      pointer-events: none;
    }

    .purchase-button, .purchase-button > button {
      display: block;
      width: 100%;
    }
  }

}


.number-component {
  margin-bottom: 4px;

  .input {
    width: 60px;
    vertical-align: 0.5px;
    text-align: center;
  }

  button {
    line-height: 1.5;
  }

  & > span {
    padding: 0 4px;
  }
}

.balance-component {
  position: fixed;
  bottom: 0px;
  height: 80px;
  width: 100%;
  background: #40a9ff;
  color: white;
  font-size: 30px;
  font-weight: 200;
  padding: 16px 30px;

  span {
    font-weight: bold;
    padding-left: 10px;
  }
}

.ant-layout-footer {
  padding-bottom: 60px;
}

.spin-div {
  padding: 20px;
  text-align: center;
}

.username-display {
  position: absolute;
  right: 50px;
  top: 80px;
  color: #AAA;

  .username-text {
    padding-left: 4px;
    vertical-align: -4px;
    font-size: 10px;
    width: 120px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: inline-block;
    white-space: nowrap;
  }

  .reload-btn {
    cursor: pointer;
    padding-left: 4px;
    padding-right: 2px;
  }
}
